@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@400;500;700&display=swap);

// TEXT
@mixin h1(){
    font-family: 'Red Hat Text', sans-serif;
    font-size: 6rem;
    font-weight: 500;
    color: $black;
    padding-bottom: 3rem;
} 

@mixin h2(){
    font-family: 'Red Hat Text', sans-serif;
    font-size: 2rem;
    font-weight: bold;
    color: $grey;
} 

@mixin p(){
    font-family: 'Red Hat Text', sans-serif;
    font-size: 1.7rem;
    color: $black;
    line-height: 1.6;
} 

@mixin p-small(){
    font-family: 'Red Hat Text', sans-serif;
    font-size: 1rem;
    color: $grey;
} 

// COLORS
$black: black;
$white: white;
$light-blue: #e9fbff;
$grey: #3E3E3E;
$light-grey: #7e7e7e;


// BORDER-RADIUS
$border-radius: 90px;

// MEDIA-QUERYS
// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

// Small devices
@mixin sm {
    @media (min-width: #{$screen-sm-min}) {
        @content;
    }
}

 // Medium devices
@mixin md {
    @media (min-width: #{$screen-md-min}) {
        @content;
    }
}

 // Large devices
@mixin lg {
    @media (min-width: #{$screen-lg-min}) {
        @content;
    }
}

 // Extra large devices
@mixin xl {
    @media (min-width: #{$screen-xl-min}) {
        @content;
    }
}


